<template>
  <div>
    <div
      :class="{hide: item.deleted,active: selected === index}"
      ref="tabWidth"
      class="tabTit">
      <input @change="onNameChanged"
             @click="changeTab(index)"
             @keydown="changeWidth"
             :readonly="readonly"
             :class="{inputHover:!readonly}"
             v-model="item.filename"
             ref="inputWidth"
             type="text"/>
      <i class="el-icon-delete" v-show="deleteVisible" @click="deleteFiles(index)"></i>
      <span class="nameWidth" ref="widthName">{{item.filename}}</span>
    </div>
  </div>

</template>
<script>
  import bus from "@/bus";

  export default {
    name: "TabTit",
    props: ["item", "index", "selected","deleteVisible","repeatable"],
    data(){
      return {
        readonly:true,
        isHnputHover:false,
        note:""
      }
    },
    computed:{
      deletable(){
        return this.item.deleted && this.item.filename === ""
      },
      editable(){
        return this.item.oldname !== this.item.filename
      },
      addable(){
        return this.item.oldname === ""
      }
    },
    watch:{
      repeatable(){
        if(this.repeatable){
          this.$message.error('文件名字不能重复');
        }
      }
    },
    mounted(){
      this.$refs.inputWidth.style.width= "60px";
      this.$refs.tabWidth.style.width= "106px";
    },
    methods: {
      deleteFiles(index){
        this.$set(this.item, "deleted", true);
        this.$set(this.item,"filename","");
        bus.$emit("onDeleteFiles");
      },
      changeTab(index){
        this.readonly = true;
        this.readonly = false;
        this.$emit("onchangeTab",index);
      },
      onNameChanged(){
        this.$set(this.item, "editable", true);
        bus.$emit("onNameChangedHandle",{
          deletable:this.deletable,
          editable:this.editable,
        });
//        filename是否重复
        bus.$emit("repeatFilename",this.item.filename);

      },
      changeWidth(){
        this.$refs.inputWidth.style.width = this.$refs.widthName.offsetWidth+20+"px";
        this.$refs.tabWidth.style.width = this.$refs.widthName.offsetWidth+70+"px";
      }
    }
  }
</script>
<style scoped>
  .nameWidth{
    position: relative;
    top:0;
    right:1000000px;
  }
  .tabTit{
    position: relative;
    margin-right: 20px;
  }
  .active{
    background-color: #fff;
    border-radius: 4px 4px 0 0;
  }
  .active input
  {
    background-color: #fff;
    border:1px solid #fff;
  }
  input {
    background-color: #f5f5f5;
    border:none;
    border-radius:4px;
    padding: 5px 10px;
    outline: none;
    margin-right: 10px;
    position: relative;
  }
  .el-icon-delete {
    position: absolute;
    top: 8px;
    right: 12px;
    font-size: 12px;
    cursor: pointer;
  }
</style>



