<template>
  <div>
    <contentView
      :item="item"
      :index="index"
      v-show="selected === index"
      :language="item.language">
    </contentView>
  </div>
</template>

<script>
  import bus from './../../bus.js'
  import contentView from '@/components/List/contentView'

  export default {
    name: "File",
    props:["item","index"],
    components:{contentView},
    data(){
      return {
        selected:0
      }
    },
    mounted(){
      bus.$on("changeContent",(index)=>{
        this.selected = index;
      })
      bus.$on("addContent",(index) =>{
        this.selected = index;
      });
      bus.$on("defaultContent",()=>{
        this.selected = 0;
      });
    }
  }
</script>

<style scoped>

</style>



